import axiosInstance from '../../../utils/axios.middleware.js';

class City {

	static listCities = (params) => {

		let apiPath = `/api/city/list-cities?_pageNo=${params?._pageNo}&_count=${params?._count}`;
		return axiosInstance.get(apiPath);

	};


	static createCity = (form) => {

		let apiPath = 'api/city';
		return axiosInstance.post(apiPath, {
			...form
		});

	};

	static deleteCity = ({ id }) => {

		let apiPath = `api/city/${id}`;
		return axiosInstance.delete(apiPath);

	};

}
export default City;