import City from "../services/city/city.js";
import actionErrorHandler from '../../helpers/actionErrorHandler.js';

export const listCities = (params) => {
	return City.listCities(params)
		.then((response) => {
			return {
				status: response.status,
				data: response.data,
				message: "success",
			};
		})
		.catch((e) => {
			return actionErrorHandler(e);
		});
};

export const createCity = (form) => {

	return City.createCity(form).then(response => {
		return { status: response.status, data: response.data, message: "Request Success" };
	}).catch(e => {
		return actionErrorHandler(e);
	});

};

export const deleteCity = ({ id }) => {

	return City.deleteCity({ id }).then(response => {
		return { status: response.status, data: response.data, message: "Request Success" };
	}).catch(e => {
		return actionErrorHandler(e);
	});

};
